import React, { useEffect, useState } from "react";
import { useFormik } from "formik";
import * as Yup from "yup";
import { toast } from "react-toastify";
// import Processing from "../../components/processing/processing";
import { useLocation, useNavigate } from "react-router-dom";
import { API } from "../../api/api";
import constants from '../../utils/contants';

const DriverDetails = () => {

    const [isEditable, setIsEditable] = useState(false);
    const [buttonValue, setButtonValue] = useState("Edit");
    // const [loading, setLoading] = useState(false);
    const navigate = useNavigate();
    const { state } = useLocation();
    const { driver } = state;

    useEffect(() => {
        getUserData();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const getUserData = () => {
        // setLoading(true);
        formik.setFieldValue("firstname", driver?.firstname);
        formik.setFieldValue("lastname", driver?.lastname);
        formik.setFieldValue("vehicle", driver?.car_info?.car_type?.name);
        formik.setFieldValue("email", driver?.email);
        formik.setFieldValue("phone", driver?.phone_no);
        // setLoading(false);
    };

    const validationSchema = Yup.object().shape({
        firstname: Yup.string().required("Please Enter Username"),
        lastname: Yup.string().required("Please Enter Username"),
        email: Yup.string().required("Please Enter email"),
        phone: Yup.number().required().positive().integer(),
        vehicle: Yup.string().required("Please Enter Vehicle"),
    });

    const formik = useFormik({
        initialValues: {
            firstname: "",
            lastname: "",
            email: "",
            vehicle: "",
            phone: "",
        },
        validationSchema: validationSchema,
        onSubmit: async (values) => {
            console.log("values: ", values);
            const body = {
                // id: driver?._id,
                firstname: values?.firstname,
                lastname: values?.lastname,
                email: values?.email,
                car_info : {
                    car_type : {
                        name: values?.vehicle,
                    }
                },
                phone_no: values?.phone,
            };
            API(`update-driver/${driver?._id}`, "put", body, localStorage.getItem(constants.TOKEN))
                .then((res) => {
                    console.log("res: ", res);
                    toast.success("Driver Details updated successfully ✅", {
                        position: toast.POSITION.TOP_RIGHT,
                        className: "bg-success text-white",
                    });
                    navigate("/manage-driver");
                })
                .catch((error) => {
                    toast.error("Error while updating Driver Details", {
                        position: toast.POSITION.TOP_RIGHT,
                    });
                    console.log("error: ", error);
                });
        },
    });

    const enableEditing = () => {
        setIsEditable(true);
        setButtonValue("Update");
    };

    return (
        <div className="ta__dashboard">
            <div className="ta__dashboard__breadcrumb">
                <h2 className="f-24">Manage Drivers</h2>
                <nav aria-label="breadcrumb">
                    <ol className="breadcrumb mb-0">
                        <li className="breadcrumb-item f-18">
                            <a href="/" className="text-gray">
                                Home
                            </a>
                        </li>
                        <li
                            className="breadcrumb-item f-18 text-black active"
                            aria-current="page"
                        >
                            Library
                        </li>
                    </ol>
                </nav>
            </div>
            <div className="ta__dashboard__body pb-5">
                {/* {loading && <Processing />} */}
                {/* header */}
                <div className="title">Driver Details</div>

                <div className="advice_form text-black">
                    <div className="d-flex flex-column mt-3">
                        <label className="category_label">First Name</label>
                        <input
                            type="text"
                            name="firstname"
                            disabled={isEditable ? false : true}
                            className="form form-control bg-transparent text-black"
                            // {...formik.getFieldProps("username")}
                            value={formik.values.firstname}
                            onChange={(e) => {
                                formik.setFieldValue("firstname", e.target.value?.trimStart())
                            }}
                        />
                        {formik.touched.firstname && formik.errors.firstname && (
                            <div className="text-danger">{formik.errors.firstname}</div>
                        )}
                    </div>
                    <div className="d-flex flex-column mt-3">
                        <label className="category_label">Last Name</label>
                        <input
                            type="text"
                            name="lastname"
                            disabled={isEditable ? false : true}
                            className="form form-control bg-transparent text-black"
                            // {...formik.getFieldProps("username")}
                            value={formik.values.lastname}
                            onChange={(e) => {
                                formik.setFieldValue("lastname", e.target.value?.trimStart())
                            }}
                        />
                        {formik.touched.lastname && formik.errors.lastname && (
                            <div className="text-danger">{formik.errors.lastname}</div>
                        )}
                    </div>
                    <div className="d-flex flex-column mt-3">
                        <label className="category_label">Email</label>
                        <input
                            type="text"
                            name="email"
                            disabled={isEditable ? false : true}
                            className="form form-control bg-transparent text-black type_password"
                            // {...formik.getFieldProps("email")}
                            value={formik.values.email}
                            onChange={(e) => {
                                formik.setFieldValue("email", e.target.value?.trimStart())
                            }}
                        />
                        {formik.touched.email && formik.errors.email && (
                            <div className="text-danger">{formik.errors.email}</div>
                        )}
                    </div>
                    <div className="d-flex flex-column mt-3">
                        <label className="category_label">Phone No.</label>
                        <input
                            type="text"
                            name="phone"
                            disabled={isEditable ? false : true}
                            className="form form-control bg-transparent text-black"
                            // {...formik.getFieldProps("username")}
                            value={formik.values.phone}
                            onChange={(e) => {
                                formik.setFieldValue("phone", e.target.value?.trimStart())
                            }}
                        />
                        {formik.touched.phone_no && formik.errors.phone_no && (
                            <div className="text-danger">{formik.errors.phone_no}</div>
                        )}
                    </div>
                    <div className="d-flex flex-column mt-3">
                        <label className="category_label">Vehicle</label>
                        <input
                            type="text"
                            name="vehicle"
                            disabled={isEditable ? false : true}
                            className="form form-control bg-transparent text-black"
                            // {...formik.getFieldProps("username")}
                            value={formik.values.vehicle}
                            onChange={(e) => {
                                formik.setFieldValue("vehicle", e.target.value?.trimStart())
                            }}
                        />
                        {formik.touched.car_info?.car_type?.name && formik.errors.car_info?.car_type?.name && (
                            <div className="text-danger">{formik.errors.car_info?.car_type?.name}</div>
                        )}
                    </div>
                    {/* <div className="d-flex flex-column mt-3">
                        <label className="category_label">Status</label>
                        <select
                            type="text"
                            name="status"
                            disabled={isEditable ? false : true}
                            className="form form-control bg-transparent text-black"
                            // {...formik.getFieldProps("username")}
                            value={formik.values.phone}
                            onChange={(e) => {
                                formik.setFieldValue("username", e.target.value?.trimStart())
                            }} >
                                <option value="">Approve</option>
                                <option value="">Reject</option>
                                <option value="">Pending</option>
                        </select>
                        {formik.touched.phone_no && formik.errors.phone_no && (
                            <div className="text-danger">{formik.errors.phone_no}</div>
                        )}
                    </div> */}




                    <div className="d-flex mt-5 justify-content-end gap-4">
                        <button
                            type="button"
                            className="form_button btn advice__category"
                            onClick={() => {
                                window.history.back();
                            }}
                        >
                            Back
                        </button>
                        <button
                            className="form_button btn advice__category"
                            disabled={
                                buttonValue === "Update" &&
                                formik.values.firstname === driver?.firstname &&
                                formik.values.lastname === driver?.lastname &&
                                formik.values.email === driver?.email &&
                                formik.values.phone === driver?.phone_no &&
                                formik.values.vehicle === driver?.car_info?.car_type?.name
                            }
                            onClick={
                                buttonValue === "Update" ? formik.handleSubmit : enableEditing
                            }
                        >
                            {buttonValue}
                        </button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default DriverDetails