import React, { useEffect, useState } from 'react'
import RiderTable from '../../components/riderTable/RiderTable'
import axios from 'axios'

const ManageRider = () => {

  const [myData, setMyData] = useState([])
  const getApiData = async () => {
    try {
      // const res = await axios.get('http://192.168.29.59:3001/api/v1/admin/get-all-users');
      const res = await axios.get('https://countriesnow.space/api/v0.1/countries/states');
      const allCountriesData = res.data.data;

      const targetCountryName = 'India';

      const targetCountry = allCountriesData.find(country => country.name === targetCountryName);

      if (targetCountry) {
        const statesOfTargetCountry = targetCountry.states;
        setMyData(statesOfTargetCountry);
      } else {
        console.log(`Country "${targetCountryName}" not found.`);
      }
    } catch (error) {
      console.log("error", error);
    }
  }

  useEffect(() => {
    getApiData();
  },[])

  return (
    <div className="ta__dashboard">
      <div className="ta__dashboard__breadcrumb">
        <h2 className="f-24">Manage Customers</h2>
        <nav aria-label="breadcrumb">
          <ol className="breadcrumb mb-0">
            <li className="breadcrumb-item f-18">
              <a href="/" className="text-gray">
                Home
              </a>
            </li>
            <li
              className="breadcrumb-item f-18 text-black active"
              aria-current="page"
            >
              Library
            </li>
          </ol>
        </nav>
      </div>
      <div className="ta__dashboard__body">
        <div className="ta__dashboard_sort">
          <form action="" className="d-flex flex-wrap">
            <div className="col-md-4 col-sm-6 col-12">
              <div className="form_control">
                <label htmlFor="">Status</label>
                <div className="ta__country_dropdown">
                  <select className="" name="role">
                    <option value="all">All</option>
                    <option value="on-hold">On hold</option>
                    <option value="Pending">Pending</option>
                    <option value="In progress">In progress</option>
                    <option value="Pending Docs">Pending Docs</option>
                    <option value="Processing in progress">
                      Processing in progress
                    </option>
                    <option value="Invalid documents">Invalid documents</option>
                    <option value="Valid documents">Valid documents</option>
                    <option value="Training planned">Training planned</option>
                    <option value="Absent from training">
                      Absent from training
                    </option>
                    <option value="Rejected post approval">
                      Rejected post approval
                    </option>
                    <option value="Sign contract">Sign contract</option>
                    <option value="Rejected">Rejected</option>
                    <option value="Approved">Approved</option>
                  </select>
                </div>
              </div>
            </div>
            <div className="col-md-4 col-sm-6 col-12">
              <div className="form_control">
                <label htmlFor="">Service</label>
                <div className="ta__country_dropdown">
                  <select className="" name="role">
                    <option value="">All</option>
                    <option value="">USA</option>
                    <option value="">India</option>
                  </select>
                </div>
              </div>
            </div>
            <div className="col-md-4 col-sm-6 col-12">
              <div className="form_control">
                <label htmlFor="">Driver</label>
                <div className="ta__country_dropdown">
                  <select className="" name="role">
                    <option value="">All</option>
                    <option value="">USA</option>
                    <option value="">India</option>
                  </select>
                </div>
              </div>
            </div>
            <div className="col-md-4 col-sm-6 col-12">
              <div className="form_control">
                <label htmlFor="district">District</label>
                <div className="ta__country_dropdown">
                  <select className="" name="district">
                    <option value="All">All</option>
                    {
                      myData.map((district,i) => (
                        <option value={district.name} key={i}> {district.name}</option>
                      ))
                    }
                  </select>
                </div>
              </div>
            </div>
            <div className="col-md-4 col-sm-6 col-12">
              <div className="form_control">
                <label htmlFor="">Suspended reason</label>
                <div className="ta__country_dropdown">
                  <select className="" name="role">
                    <option value="">All</option>
                    <option value="DRIVER_UNVERIFIED">DRIVER_UNVERIFIED</option>
                    <option value="EXPIRED_CONTRACT">EXPIRED_CONTRACT</option>
                    <option value="NOT_VERIFIED">NOT VERIFIED</option>
                    <option value="REASON_1">REASON_1</option>
                    <option value="REASON_2">REASON_2</option>
                    <option value="TOTO">TOTO</option>
                    <option value="TRIP_DRIVER_CANCELED_10">
                      TRIP_DRIVER_CANCELED_10
                    </option>
                    <option value="TRIP_DRIVER_COMING_CANCELED_20">
                      TRIP_DRIVER_COMING_CANCELED_20
                    </option>
                    <option value="TRIP_DRIVER_COMING_CANCELED_60">
                      TRIP_DRIVER_COMING_CANCELED_60
                    </option>
                    <option value="TRIP_DRIVER_COMING_RIDER_CANCELED_60">
                      TRIP_DRIVER_COMING_RIDER_CANCELED_60
                    </option>
                    <option value="UNPAID">UNPAID</option>
                  </select>
                </div>
              </div>
            </div>

          </form>
        </div>

        <RiderTable />
      </div>
    </div>
  )
}

export default ManageRider