import "./css/style.css";
import {
  Navigate,
  Route,
  Routes,
  useLocation,
  useNavigate,
} from "react-router-dom";
import DashBoard from "./pages/dashboard/DashBoard";
import Sidebar from "./components/sidebar/Sidebar";
import LoginPage from "./pages/login/LoginPage";
import Error from "./pages/error/error";
import PrivateRoute from "./route/RouteProtection";
import { useEffect } from "react";
import constants from "./utils/contants";
import ManageDriver from "./pages/managedriver/ManageDriver";
import ManageRider from "./pages/managerider/ManageRider";
import RiderDetails from "./components/riderDetails/RiderDetails";
import DriverDetails from "./components/driverDetails/DriverDetails";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import AllRides from "./pages/allRides/AllRides";

function App() {
  const currentPath = useLocation();
  const navigate = useNavigate();
  useEffect(() => {
    if (localStorage.getItem(constants.TOKEN)) {
      navigate("/");
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <>
    <ToastContainer />
      {currentPath.pathname !== "/login" && currentPath.pathname !== "/404" && (
        <Sidebar />
      )}
      <Routes>
        <Route
          path="/"
          element={
            <PrivateRoute>
            <DashBoard />
            </PrivateRoute>
          }
        />
        <Route
          path="/manage-driver"
          element={
            // <PrivateRoute>
            <ManageDriver />
            // </PrivateRoute>
          }
        />
        <Route
          path="/manage-driver/:id"
          element={
            // <PrivateRoute>
            <DriverDetails />
            // </PrivateRoute>
          }
        />
        <Route
          path="/all-rides"
          element={
            // <PrivateRoute>
            <AllRides />
            // </PrivateRoute>
          }
        />
        <Route
          path="/manage-rider"
          element={
            // <PrivateRoute>
            <ManageRider />
            // </PrivateRoute>
          }
        />
        <Route
          path="/manage-rider/:id"
          element={
            // <PrivateRoute>
            <RiderDetails />
            // </PrivateRoute>
          }
        />
        <Route path="/login" element={<LoginPage />} />
        <Route path="/404" element={<Error />} />
        <Route path="*" element={<Navigate to="/404" />} />
      </Routes>
    </>
  );
}

export default App;
